import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Publication from './Publication/Publication';
import Counter from './Counter/Counter';
import Controls from './Controls/Controls';
import items from '../../assets/publications.json';
import getPageFromLocation from '../../helpers/helpers';

import styles from './Reader.module.css';

class Reader extends Component {
  static propTypes = {
    history: PropTypes.shape({
      push: PropTypes.func.isRequired,
    }).isRequired,
    location: PropTypes.shape({
      search: PropTypes.string.isRequired,
    }).isRequired,
  };

  componentDidMount() {
    const { history, location } = this.props;
    const searchPage = Number(getPageFromLocation(location));

    const searchPageNotOk = !searchPage || searchPage > items.length;

    console.log(searchPage, searchPageNotOk);

    return searchPageNotOk
      ? history.push({
          ...location,
          search: 'page=1',
        })
      : history.push({
          ...location,
          search: `page=${searchPage}`,
        });
  }
  // http://localhost:3000/#/reader?page=22

  handleControlButton = ({ target: { name } }) => {
    const { history, location } = this.props;
    const searchPage = Number(getPageFromLocation(location));

    history.push({
      ...location,
      search: `page=${name === 'next' ? searchPage + 1 : searchPage - 1}`,
    });
  };

  render() {
    const { location } = this.props;
    const currentPage = Number(getPageFromLocation(location)) - 1 || 0;

    return (
      <div className={styles.reader}>
        <Controls
          currentPage={currentPage}
          maxPage={items.length}
          handleControlButton={this.handleControlButton}
        />
        <Counter currentPage={currentPage} maxPage={items.length} />
        <Publication item={items[currentPage]} />
      </div>
    );
  }
}

export default Reader;
